<template>
        <div>
            <div v-for="(item,index) in rooms" :key="index">
                <h4 class="text-primary font-weight-normal p-2">{{item.title}}</h4>
                <vue-cal :selected-date="today"
                :time="false"
                hide-view-selector
                :disable-views="['years']"
                default-view="month"
                events-on-month-view="short"
                :events="events"
                style="height:250px">
                </vue-cal>
            </div>
        </div>
</template>
<script>
import VueCal from 'vue-cal'
import 'vue-cal/dist/vuecal.css'
var moment = require('moment');

export default {
    props:{
        rooms:{
            type:Array,
            default:function(){
                return [];
            }
        }
    },
    data() {
        return {
            today:moment().format('YYYY-MM-DD'),
            events: [
                {
                start: '2019-01-01',
                end: '2019-12-31',
                title: 20,
                content: '<i class="v-icon material-icons">shopping_cart</i>',
                class: 'dorm'
                },
                {
                start: '2019-01-01',
                end: '2019-12-31',
                title: 340,
                content: '<i class="v-icon material-icons">shopping_cart</i>',
                class: 'price'
                },
            ]
        }
    },
    components: {
        'vue-cal': VueCal
    },
  // ...
};
</script>
<style lang="scss">
.vuecal__menu, 
.vuecal__cell-events-count {
    background-color: #2E688C;
}
.vuecal__title-bar{
    background-color: #BCBBF2;
}
.vuecal__cell.today, 
.vuecal__cell.current {
    background-color: #D5D7F2;
}
.vuecal__event.dorm {
    background-color: rgba(253, 156, 66, 0.9);
    border: 1px solid rgb(233, 136, 46);
    color: #fff;
}
.vuecal__event.price {
    background-color: rgba(255, 102, 102, 0.9)
    ;border: 1px solid rgb(235, 82, 82);
    color: #fff;
}
.vuecal__event-title{
    text-align: center !important;
}
</style>



